.datetime-input {
  background-color: #fff;
  border-radius: .4rem;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

.datetime-input__now-button {
  background-color: transparent;
  border: none;
  color: #757575;
  font-weight: bold;
  padding: 0 .8rem;
}

.datetime-input__now-overlay {
  -ms-flex-align: center;
      align-items: center;
  background-color: #fff;
  bottom: .8rem;
  display: -ms-flexbox;
  display: flex;
  left: 2.4rem;
  pointer-events: none;
  position: absolute;
  right: .8rem;
  top: .8rem;
}



